import events from './events'


// dados privados
const data = {
  count: 0
}

/**
 * Singleton para controle de status de carregamento da aplicao.
 *
 * Eventos:
 *   - @loading:start
 *       Incio de carregamento
 *
 *   - @loading:stop
 *       Encerramento de carregamento
 *
 * @type {Object}
 */
const loading = {
  /**
   * Informa o estado de carregamento da aplicao
   * @return {Boolean} estado de carregamento
   */
  get active() {
    return data.count > 0
  },

  /**
   * Inicia um carregamento.
   * IMPORTANTE isso acrescenta um novo carregamento (contador) ao controle
   * interno do mdulo.
   */
  start() {
    data.count++

    events.$emit('loading:start')
  },

  /**
   * Encerra um carregamento. Se a opo `all` for definida como `true`,
   * encerra todos os carregamentos em andamento (contador). Seno, encerra
   * apenas um na contagem
   *
   * @param  {Boolean} all Controla o encerramento de todos os carregamentos. Default - false.
   */
  stop(all = false) {
    if (all) {
      data.count = 0
    } else {
      data.count--
    }

    events.$emit('loading:stop')
  }
}

export default loading
