import events from './events'

/**
 *  Singleton para controle de notificaes da aplicao, disparando os eventos
 * necessrios.
 *
 * IMPORTANT utilize o componente `notifications.vue` para integrar com a UI
 *
 * @type {Object}
 */
const notifications = {
  /**
   *   Remove todas as notificaes existentes.
   */
  clear() {
    events.$emit('notifications:clear')
  },

  /**
   *  Adiciona uma notificao com level "error" - til para informar falhas
   * em algum processamento (ex: salvar um cadastro)
   *
   * @param  {String}  message contedo da notificao
   * @param  {Boolean} dismiss requer remoo automtica. Default: true.
   */
  error(message, dismiss = true) {
    events.$emit('notifications:error', message, dismiss)
  },

  /**
   *  Adiciona uma notificao com level "info" - til para informar sucesso
   * de algum processamento (ex: salvar um cadastro)
   *
   * @param  {String}  message contedo da notificao
   * @param  {Boolean} dismiss requer remoo automtica. Default: true.
   */
  info(message, dismiss = true) {
    events.$emit('notifications:info', message, dismiss)
  },

  /**
   *  Adiciona uma notificao com level "warn" - til para informar alguma
   * "condio adversa" (ex: falha em API de terceiros)
   *
   * @param  {String}  message contedo da notificao
   * @param  {Boolean} dismiss requer remoo automtica. Default: true.
   */
  warn(message, dismiss = true) {
    events.$emit('notifications:warning', message, dismiss)
  }
}

export default notifications
