import _ from 'lodash'
import camelizeKeys from './camelize-keys'
import deleteBlanks from './delete-blanks'
import dig from './dig'
import { isBlank, isPresent } from './is-blank'
import snakeizeKeys from './snakeize-keys'

const lodashExt = Object.assign({}, _, {

  // transformando keys/props de objetos, facilitando trnsito Rails <-> JS
  camelizeKeys,
  snakeizeKeys,
  deleteBlanks,
  dig,

  // rails like #blank? and #present?
  isBlank,
  isPresent,

  // alguns alias para facilitar o trnsito Rails <-> JS
  camelize:   _.camelCase,
  capitalize: _.capitalize, // para deixar explcito
  dasherize:  _.kebabCase,
  underscore: _.snakeCase,
  snakeize:   _.snakeCase,

  clone:    _.cloneDeep,
  extend:   _.extend,     // para deixar explcito
  merge:    _.merge,      // para deixar explcito
  equals:   _.isEqual,
  contains: _.isMatch

})

export default lodashExt
